import './App.css';
import fourtyOne from './fourtyOne.json';
import quotes from './quotes.json';

function getRandomInt(min, max) {
  return min + Math.floor(Math.random() * (max - min + 1));
}

function App() {
  const cohortMember = getRandomInt(0,53).toString();
  return (
    <div className="App">
      <header className="App-header">
        <img src={fourtyOne[cohortMember]} className="App-logo" alt="logo" />
        <p>
          <br></br>
          <br></br>
          {(cohortMember === "35") ? "Real talk." :  quotes[getRandomInt(0,11).toString()]}
        </p>
        <a
          className="App-link"
          href="https://codesmithla41.slack.com/"
          target="_blank"
          rel="noopener noreferrer"
        >
          Cohort 41 Slack
        </a>
      </header>
    </div>
  );
}

export default App;
